import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import { createApp } from 'vue'
import App from './App.vue'
import './style.css'
import * as VueRouter from 'vue-router'
import ProjectDetailPage from './pages/ProjectDetailPage.vue'
import ContactPage from './pages/ContactPage.vue'
import ProjectsPage from './pages/ProjectsPage.vue'
import OpenPage from './pages/OpenPage.vue'
import NotFoundPage from './pages/NotFoundPage.vue'
import LoginPage from "./pages/LoginPage.vue";
import AdminPage from "./pages/AdminPage.vue";
import NewProjectPage from "./pages/NewProjectPage.vue";
import ProjectEditPage from "./pages/ProjectEditPage.vue";
import NewHistoryPage from "./pages/NewHistoryPage.vue";



import { createStore } from 'vuex';
const key = 'authentication';

const store = createStore({
  state: {
    isAuthenticated: localStorage.getItem(key) === 'true' || false,
  },
  mutations: {
    SET_AUTHENTICATION(state, isAuthenticated) {
      state.isAuthenticated = isAuthenticated;
      localStorage.setItem(key, isAuthenticated);
    },
  },
  actions: {
    updateAuthentication({ commit }, isAuthenticated) {
      commit('SET_AUTHENTICATION', isAuthenticated);
    },
    login({ commit }) {
      // Perform login logic
      commit('setAuthentication', true);
    },
    logout({ commit }) {
      // Perform logout logic
      commit('SET_AUTHENTICATION', false);
      localStorage.removeItem(key);
    },
  },
  getters: {
    isAuthenticated: (state) => state.isAuthenticated,
  },
});

const requireAuth = (to, from, next) => {
  console.log('Checking authentication...');
  const isAuthenticated = store.getters.isAuthenticated;

  if (to.meta.requiresAuth && !isAuthenticated) {
    console.log('Authentication required, redirecting to login.');
    next('/login');
  } else {
    console.log('Authentication successful, allowing access.');
    next();
  }
};

// Vue Router setup
const router = VueRouter.createRouter({
  history: VueRouter.createWebHistory(process.env.BASE_URL),
  routes: [
    {
      path: '/contact',
      component: ContactPage,
    },
    {
      path: '/projects',
      component: ProjectsPage,
    },
    {
      path: '/project/:projectModifiedName',
      component: ProjectDetailPage,
    },
    {
      path: '/',
      component: OpenPage,
    },
    {
      path: '/login',
      component: LoginPage,
      meta: { hideNavbar: true },
    },
    {
      path: '/dashboard',
      component: AdminPage,
      meta: { requiresAuth: true, hideNavbar: true },
      beforeEnter(to, from, next) {
        console.log('Route guard for /dashboard');
        const isAuthenticated = store.getters.isAuthenticated;
        if (to.meta.requiresAuth && !isAuthenticated) {
          console.log('Authentication required, redirecting to login.');
          next('/login');
        } else {
          console.log('Authentication successful, allowing access.');
          next();
        }
      },
    },
    {
      path: '/projects/new',
      component: NewProjectPage,
      meta: { requiresAuth: false, hideNavbar: true },
      /*beforeEnter(to, from, next) {
        console.log('Route guard for /projects/new');
        const isAuthenticated = store.getters.isAuthenticated;
        if (to.meta.requiresAuth && !isAuthenticated) {
          console.log('Authentication required, redirecting to login.');
          next('/login');
        } else {
          console.log('Authentication successful, allowing access.');
          next();
        }
      },*/
    },
    {
      path: '/projects/edit/:modified_name',
      name: 'projectEditPage',
      component: ProjectEditPage,
    },
    {
      path: '/histories/new',
      component: NewHistoryPage,
      meta: { requiresAuth: false, hideNavbar: true },
    },
    {
      path: '/:pathMatch(.*)*',
      component: NotFoundPage,
    },
  ],
});

router.beforeEach((to, from, next) => {
  window.scrollTo(0, 0);
  next();
})
router.beforeResolve((to, from, next) => {
  // Scroll to the top before route is resolved (including page refresh)
  window.scrollTo(0, 0);
  next();
});
// Apply the navigation guard to all routes
router.beforeEach(requireAuth);


// Create the Vue app
const app = createApp(App);

// Use the Vuex store and Vue Router
app.use(store);
app.use(router);

console.log('Vue app is mounting');
app.mount('#app');

