<template>
  <div class="admin-tab-bar">
    <div
      v-for="(tab, index) in tabs"
      :key="index"
      @click="changeTab(index)"
      :class="{ active: activeTab === index }"
    >
      {{ tab }}
    </div>
  </div>
</template>

<script>
export default {
  props: {
    tabs: Array,
    activeTab: Number,
  },
  methods: {
    changeTab(index) {
      this.$emit("change-tab", index);
    },
  },
};
</script>

<style scoped>
/* Add your custom styles here */
.admin-tab-bar {
  display: flex;
  background-color: #f0f0f0;
}

.admin-tab-bar div {
  flex: 1;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  border-bottom: 2px solid transparent;
}

.admin-tab-bar div.active {
  border-bottom: 2px solid #4caf50; /* Active tab color */
}
</style>
