<template>
  <div ref="map" style="height: 400px"></div>
</template>

<script>
import L from "leaflet";
import "leaflet/dist/leaflet.css";

export default {
  mounted() {

    const initialCoordinates = [58.37326999837861, 26.71988304648635]; // Example: New York City coordinates
    // Create a Leaflet map
    const map = L.map(this.$refs.map).setView(initialCoordinates, 18);
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: '© OpenStreetMap contributors',
    }).addTo(map);

    const customIcon = L.icon({
      iconUrl: require('../assets/marker.png'), // Path to your marker image
      iconSize: [50, 50], // Size of the marker icon
      iconAnchor: [15, 30], // Anchor point of the icon which is the bottom center
      popupAnchor: [0, -30], // Anchor point for popups (relative to the icon anchor)
    });

    const initialMarker = L.marker(initialCoordinates, { icon: customIcon }).addTo(map);
    initialMarker.bindPopup('Riia 15b').openPopup();
  },
};
</script>

