<template>
  <div>
    <h1>Welcome to the Admin Page</h1>
    <button @click.prevent="$router.push('/login')">Log Out</button>

    <div class="contact-form-container row align-items-center justify-content-center">
      <div class="form-card col-md-6 p-4">
        <form @submit.prevent="createProject" class="row g-3 p-4">
          <div class="col-md-6 py-2">
            <label for="name" class="form-label">Name:</label>
            <input
              type="text"
              class="form-control"
              id="name"
              v-model="formData.name"
              required
            />
          </div>
          <div class="col-md-6 py-2">
            <label for="modified_name" class="form-label">Modified name:</label>
            <input
              type="text"
              class="form-control"
              id="modified_name"
              v-model="formData.modified_name"
              required
            />
          </div>

          <div class="col-md-6 py-2">
            <label for="imageURL" class="form-label">Image URL:</label>
            <input
              type="text"
              class="form-control"
              id="imageURL"
              v-model="formData.imageURL"
              required
            />
          </div>

          <div class="col-md-6 py-2">
            <label for="websiteURL" class="form-label">WebsiteURL:</label>
            <input
              type="text"
              class="form-control"
              id="websiteURL"
              v-model="formData.websiteURL"
            />
          </div>

          <div class="col-12 py-2">
            <label for="description" class="form-label">Description:</label>
            <textarea
              id="description"
              class="form-control"
              v-model="formData.description"
              required
            ></textarea>
          </div>

          <div class="col-12">
            <button type="submit" class="btn btn-primary">Submit</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "NewProjectPage",
  data() {
    return {
      formData: {
        name: "",
        modified_name: "",
        imageURL: "",
        description: "",
        websiteURL: "",
      },
    };
  },
  components: {},

  methods: {
    async createProject() {
      // Implement logic to send a POST request to your server to create a new project
      console.log(this.formData.name)
      try {
        const response = await axios.post("/api/projects", {
          name: this.formData.name,
          modified_name: this.formData.modified_name,
          description: this.formData.description,
          imageURL: "/images/" + this.formData.imageURL,
          websiteURL: this.formData.websiteURL,
          // Add other fields as needed
        });

        const data = response.data;

        if (data.success) {
          // Project created successfully, you might want to redirect or show a success message
          console.log("Project created successfully");
          // Redirect to the dashboard
          this.$router.push("/dashboard");

        } else {
          // Failed to create project, handle the error
          console.error("Failed to create project");
        }
      } catch (error) {
        console.error("Error creating project - Status Code:", error.response.status);
      }
    },
    async logout() {
      // Send a DELETE request to your Express server
      const response = await fetch("/api/logout", { method: "POST" });
      this.$store.getters.isAuthenticated = false;
      // Check if the logout was successful
      if (response.ok) {
        // Dispatch the logout action to update the authentication state
        this.$store.dispatch("logout");
        console.log("Authentication state:", this.$store.getters.isAuthenticated);
      } else {
        // Handle errors if needed
        console.error("Logout failed:", response.statusText);
      }
    },
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters.isAuthenticated;
    },
  },
  watch: {
    isAuthenticated(newValue) {
      console.log("Authentication state:", newValue);
    },
  },
};
</script>
