<template>
  <div>
    <h1>Welcome to the Admin Page</h1>
    <button @click.prevent="$router.push('/login')">Log Out</button>

    <div class="contact-form-container row align-items-center justify-content-center">
      <div class="form-card col-md-6 p-4">
        <form @submit.prevent="updateProject" class="row g-3 p-4">
          <div class="col-md-6 py-2">
            <label for="name" class="form-label">Name:</label>
            <input class="form-control" v-model="editedProject.name" type="text" required />
          </div>
          <div class="col-md-6 py-2">
            <label for="modifiedName" class="form-label">Modified Name:</label>
            <input class="form-control" v-model="editedProject.modified_name" type="text" required />
          </div>

          <div class="col-md-6 py-2">
            <label for="imageURL" class="form-label">Image URL:</label>
            <input class="form-control" v-model="editedProject.imageURL" type="text" />
          </div>

          <div class="col-md-6 py-2">
            <label for="websiteURL" class="form-label">WebsiteURL:</label>
            <input class="form-control" v-model="editedProject.websiteURL" type="text"/>
          </div>

          <div class="col-12 py-2">
            <label for="description" class="form-label">Description:</label>
            <textarea class="form-control" v-model="editedProject.description" required></textarea>
          </div>

          <div class="col-12">
            <button type="submit" class="btn btn-primary">Update</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ProjectEditPage",
  data() {
    return {
      projectModifiedName: null,
      editedProject: {
        name: "",
        modified_name: "",
        imageURL: "",
        description: "",
        websiteURL: "",
      },
      loading: true,
    };
  },
  created() {
    this.projectModifiedName = this.$route.params.modified_name;
    console.log(this.projectModifiedName);
    this.fetchProjectDetails();
    console.log(this.editedProject);
  },
  methods: {
    async fetchProjectDetails() {
      try {
        const response = await axios.get(`/api/project/${this.projectModifiedName}`);
        this.editedProject = response.data; // Assuming the response is an object with project details
        console.log(this.editedProject);
        this.loading = false;
      } catch (error) {
        console.error("Error fetching project details:", error);
        // Handle error, show a message, etc.
      }
    },
    async updateProject() {
      try {
        if (!this.editedProject) {
          console.error("editedProject is not initialized properly");
          return;
        }

        // Update properties of editedProject using form data

        console.log(this.editedProject);

        const response = await axios.put(
          `/api/projects/${this.projectModifiedName}`,
          this.editedProject
        );

        if (response.status === 200 && response.data.success) {
          // Display a success message or navigate back to the projects page
          console.log("Project updated successfully");
          this.$router.push({ path: "/dashboard" });
        } else {
          // Display an error message
          console.error(
            "Failed to update project:",
            response.data.message || "Unknown error"
          );
        }
      } catch (error) {
        // Display an error message
        console.error("Error updating project:", error);
      }
    },
  },
};
</script>
