<template>
  <div>
    <h1>Welcome to the Admin Page</h1>
    <button @click.prevent="$router.push('/login')" class="mb-3">Log Out</button>
    <admin-tab-bar
      :tabs="['Projects', 'News', 'Histories', 'Consultations']"
      :active-tab="activeTab"
      @change-tab="changeTab"
    />
    <div
      v-show="activeTab === 0"
      class="w-75 justify-content-center align-items-center mx-auto mt-5"
    >
      <button v-on:click.prevent="$router.push('/projects/new')" class="mb-3">
        Create
      </button>
      <div class="row flex-grow-1 mr-4">
        <div v-for="(project, index) in projects" :key="index" class="col-md-3 mb-4">
          <div class="card">
            <img
              :src="project.imageURL"
              alt="Project Image"
              class="card-img-top"
              style="max-height: 200px"
            />
            <div class="card-body">
              <h5 class="card-title">{{ project.name }}</h5>
              <p class="card-text">{{ project.description }}</p>
              <div class="d-flex justify-content-between">
                <button @click="editProject(index)" class="btn btn-primary">Edit</button>
                <button @click="deleteProject(index)" class="btn btn-danger">
                  Delete
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-show="activeTab === 1">
      <div class="row flex-grow-1 mr-4">
        <div v-for="(newsOne, index) in news" :key="index" class="col-md-3 mb-4">
          <div>
            <h3>{{ newsOne.title }} - {{ newsOne.date }}</h3>
          </div>
        </div>
      </div>
    </div>
    <div
      v-show="activeTab === 2"
      class="w-75 justify-content-center align-items-center mx-auto"
    >
      <h1>Ajaloo faktid</h1>
      <button v-on:click.prevent="$router.push('/histories/new')" class="mb-3">
        Create
      </button>
      <div v-for="(history, index) in histories" :key="index" class="row p-3 bg-white rounded shadow mb-5">
        <div >
          <h6>
            {{ history.text }}
          </h6>
        </div>
        <div class="d-flex justify-content-center">
          <button @click="editHistory(index)" class="btn btn-primary">Edit</button>
          <button @click="deleteHistory(index)" class="btn btn-danger">Delete</button>
        </div>
      </div>
    </div>
    <div v-show="activeTab === 3" class="consultation-container">
      <h1>Consultation dates</h1>
      <form @submit.prevent="addConsultationDate">
        <label for="newDate">Select Consultation Date:</label>
        <input type="date" v-model="selectedDate" required />
        <button type="submit">Add</button>
      </form>

      <div
        v-if="notification"
        :class="{
          notification: true,
          success: successNotification,
          error: !successNotification,
        }"
      >
        {{ notification }}
      </div>

      <table>
        <thead>
          <tr>
            <th>Date</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(consultation, index) in consultations" :key="index">
            <td>{{ formatDate(consultation.date) }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AdminTabBar from "@/components/AdminTabBar.vue";

export default {
  name: "AdminPage",
  data() {
    return {
      projects: [],
      consultations: [],
      activeTab: 0,
      news: [
        { title: "News 1", date: "2024-01-13" },
        { title: "News 2", date: "2024-01-14" },
        // Add more news items as needed
      ],
      histories: [],
      availableDates: [], // An array of available dates from your API
      selectedDate: null, // A single date to hold the selected date
      notification: null,
      successNotification: false,
    };
  },
  components: {
    AdminTabBar,
  },
  async created() {
    try {
      const response1 = await axios.get("api/projects");
      const response2 = await axios.get("api/consultations");
      const response3 = await axios.get("api/histories");
      this.projects = response1.data;
      this.consultations = response2.data;
      this.histories = response3.data;
    } catch (error) {
      console.error("Error fetching:", error);
      // Handle error, show a message, etc.
    }
  },
  methods: {
    changeTab(index) {
      this.activeTab = index;
    },
    formatDate(date) {
      const options = { day: "2-digit", month: "long", year: "numeric" };
      const formattedDate = new Date(date).toLocaleDateString(undefined, options);
      return formattedDate;
    },
    editProject(index) {
      const projectModifiedNameToEdit = this.projects[index].modified_name;
      this.$router.push({
        name: "projectEditPage",
        params: {
          modified_name: projectModifiedNameToEdit,
        },
      });
    },
    async deleteProject(index) {
      // Confirm with the user before proceeding with the deletion
      const isConfirmed = window.confirm("Are you sure you want to delete this project?");
      if (!isConfirmed) {
        return; // Do nothing if the user cancels the deletion
      }

      const projectModifiedNameToDelete = this.projects[index].modified_name;

      try {
        const response = await axios.delete(
          `/api/project/${projectModifiedNameToDelete}`
        );
        if (response.status === 200 && response.data.success) {
          this.projects = this.projects.filter((project, i) => i !== index);
          this.successNotification = true;
          this.notification = response.data.message || "Project deleted successfully";
          setTimeout(() => {
            this.notification = null;
            this.successNotification = false;
          }, 3000);
        } else {
          this.successNotification = false;
          this.notification = response.data.message || "Error: Unknown error";

          if (response.status === 404) {
            this.projects = this.projects.filter((project, i) => i !== index);
          }
        }
      } catch (error) {
        this.successNotification = false;
        this.notification = `Error: ${
          error.response
            ? error.response.data.message || "Unknown error"
            : "Network error"
        }`;
      }
    },
    async addConsultationDate() {
      if (this.selectedDate) {
        try {
          // Make a POST request to add the consultation on the server
          const response = await axios.post("/api/consultations", {
            date: this.selectedDate,
          });

          // Check if the server responded with an error
          if (response.status === 201) {
            // Parse the response JSON and update the consultations array
            const newConsultation = response.data;
            this.consultations.push(newConsultation);
            // Clear the selected date for the next entry
            this.selectedDate = "";

            // Display a success notification on the page
            this.successNotification = true;
            this.notification = "Consultation date saved successfully";

            // Clear the notification after a few seconds
            setTimeout(() => {
              this.notification = null;
              this.successNotification = false;
            }, 3000);
          } else {
            // Display the error as a notification on the page
            this.successNotification = false;
            this.notification = `Error: ${response.data.error || "Unknown error"}`;
          }
        } catch (error) {
          // Display the error as a notification on the page
          this.successNotification = false;
          this.notification = `Error: ${
            error.response
              ? error.response.data.error || "Unknown error"
              : "Network error"
          }`;
        }
      }
    },
    saveConsultationDate(date) {
      axios
        .post("/api/consultations", { date })
        .then((response) => {
          const consultationDate = response.data;
          this.consultations.push(consultationDate); // Assuming the response is a single date object
          this.selectedDate = "";

          // Display a success notification to the user
          this.$notify({
            title: "Success",
            message: "Consultation date saved successfully",
            type: "success",
          });
        })
        .catch((error) => {
          // Display an error notification to the user
          this.$notify({
            title: "Error",
            message: `Error saving consultation date: ${error.message}`,
            type: "error",
          });
        });
    },
    async logout() {
      const response = await fetch("/api/logout", { method: "POST" });
      this.$store.getters.isAuthenticated = false;
      if (response.ok) {
        this.$store.dispatch("logout");
        console.log("Authentication state:", this.$store.getters.isAuthenticated);
      } else {
        console.error("Logout failed:", response.statusText);
      }
    },
  },
  computed: {
    notificationClass() {
      return {
        notification: true,
        success: this.successNotification,
      };
    },
    isAuthenticated() {
      return this.$store.getters.isAuthenticated;
    },
  },
  watch: {
    isAuthenticated(newValue) {
      console.log("Authentication state:", newValue);
    },
  },
};
</script>

<style>
.consultation-container {
  max-width: 600px; /* Adjust the maximum width as needed */
  margin: 0 auto; /* Center the container horizontally */
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th,
td {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
}

.notification {
  margin-top: 10px;
  padding: 10px;
  border: 1px solid;
}

.success {
  background-color: #ccffcc; /* Light green background for success */
  border-color: #00cc00; /* Dark green border for success */
  color: #006600; /* Dark green text for success */
}

.error {
  background-color: #ffcccc; /* Light red background for error */
  border-color: #ff0000; /* Dark red border for error */
  color: #ff0000; /* Dark red text for error */
}
</style>
