<template>
  <div>
    <h1>Welcome to the Admin Page</h1>
    <button @click.prevent="$router.push('/login')">Log Out</button>

    <div class="contact-form-container row m-0 justify-content-center">
      <div class="form-card col-md-6 p-4">
        <form @submit.prevent="createHistory" class="row g-3 p-4">
          <div class="col-md-6 py-2">
            <label for="imageURL" class="form-label">Image URL:</label>
            <input
              type="text"
              class="form-control"
              id="imageURL"
              v-model="formData.imageURL"
              required
            />
          </div>

          <div class="col-12 py-2">
            <label for="text" class="form-label">Text:</label>
            <textarea
              id="text"
              class="form-control"
              v-model="formData.text"
              required
            ></textarea>
          </div>

          <div class="col-12 justify-content-center align-self-center">
            <button type="submit" class="btn btn-primary align-self-center">Submit</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "NewHistoryPage",
  data() {
    return {
      formData: {
        text: "",
        imageURL: "",
      },
    };
  },
  components: {},

  methods: {
    async createHistory() {
      // Implement logic to send a POST request to your server to create a new project
      console.log(this.formData.imageURL);
      try {
        const response = await axios.post("/api/histories", {
          text: this.formData.text,
          imageURL: "/images/" + this.formData.imageURL,
          // Add other fields as needed
        });

        const data = response.data;

        if (data.success) {
          // Project created successfully, you might want to redirect or show a success message
          console.log("Project created successfully");
          // Redirect to the dashboard
          this.$router.push("/dashboard");
        } else {
          // Failed to create project, handle the error
          console.error("Failed to create history");
        }
      } catch (error) {
        console.error("Error creating project - Status Code:", error.response.status);
      }
    },
    async logout() {
      // Send a DELETE request to your Express server
      const response = await fetch("/api/logout", { method: "POST" });
      this.$store.getters.isAuthenticated = false;
      // Check if the logout was successful
      if (response.ok) {
        // Dispatch the logout action to update the authentication state
        this.$store.dispatch("logout");
        console.log("Authentication state:", this.$store.getters.isAuthenticated);
      } else {
        // Handle errors if needed
        console.error("Logout failed:", response.statusText);
      }
    },
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters.isAuthenticated;
    },
  },
  watch: {
    isAuthenticated(newValue) {
      console.log("Authentication state:", newValue);
    },
  },
};
</script>
