<template>
  <div>
    <section class="bg">
      <NavigationBar />
      <IntroSection />
    </section>
    <section style="height: 100vh">
      <div class="row w-100 pt-5 pb-5 p-0 fade-in">
        <h1>Projektid 
          <span class="text">
          <router-link to="/projects" class="button-59 fade-in"
          >Vaata lähemalt ➔
        </router-link>
          </span></h1>
      </div>

      <SlidesComponent v-if="projects.length > 0" :projects="projects" />
    </section>
    <section class="s3">
      <div class="pb-2 pt-5">
        <h1>Ajalugu</h1>
      </div>
      <TimeLine :histories="histories" @itemInView="updateHistoryInView" />
    </section>
    
    <button v-if="showButton" @click="scrollToTop" class="scroll-top-btn"></button>
  </div>
</template>

<script>
import IntroSection from "@/components/IntroSection.vue";
import SlidesComponent from "../components/SlidesComponent";
import axios from "axios";
import TimeLine from "@/components/TimeLine.vue";
import NavigationBar from "@/components/NavigationBar.vue";

export default {
  name: "OpenPage",
  data() {
    return {
      histories: [],
      projects: [],
      showButton: false,
    };
  },
  components: {
    IntroSection,
    SlidesComponent,
    TimeLine,
    NavigationBar,
  },
  async created() {
    try {
      window.scrollTo(0, 0);

      const response1 = await axios.get("api/histories");
      this.histories = response1.data;

      const response2 = await axios.get("api/projects");
      this.projects = response2.data;
    } catch (error) {
      console.error(error);
    }
  },
  methods: {
    // Function to check if the button should be shown based on scrolling position
    checkScroll() {
      if (window.pageYOffset > 100) {
        this.showButton = true;
      } else {
        this.showButton = false;
      }
    },
    // Function to scroll to the top of the page
    scrollToTop() {
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
  },
  mounted() {
    // Add event listener for scroll event
    window.addEventListener("scroll", this.checkScroll);
  },
  beforeUnmount() {
    // Remove event listener when component is destroyed
    window.removeEventListener("scroll", this.checkScroll);
  },
  updateHistoryInView(index) {
    this.$set(this.histories, index, { ...this.histories[index], inView: true });
  },
};
</script>

<style scoped>
.bg {
  background-image: url("/public/images/back2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 100vh; /* Set minimum height to 100% of the viewport height */
  width: 100%; /* Cover the entire width of the viewport */
  margin: 0; /* Remove any default margin */
  padding: 0;
  color: white
}

.s3 {
  background-color: #ebc898;
}

.scroll-top-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  /* display: none; */ /* Remove this line to ensure the button is initially visible */
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}

.scroll-top-btn::before {
  content: "";
  border: solid white;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 5px;
  transform: rotate(-135deg);
}

h1 {
  text-align: center;
  font-size: 50px;
  font-family: "Snell Roundhand", cursive;
}

.text {
  font-size: 30px;
  font-family: "Snell Roundhand", cursive;
}
</style>
